      "about_subtitle": "Votre univers de divertissement, faonn par un passionn.",
      "about_mission_title": "La Mission",
      "about_mission_desc": "La mission de ce projet est simple : offrir un accs simple, rapide et centralis  un vaste catalogue de films, sries et anims. Je crois que le divertissement doit tre accessible sans contraintes, sur n'importe quel appareil, n'importe quand. Jelly est n de cette passion pour le cinma et la technologie, avec l'ambition de crer la meilleure exprience de streaming possible.",
