      "about_passion_desc": "Je suis un dveloppeur passionn qui consacre son temps libre  crer et amliorer ce service. Chaque fonctionnalit, chaque amlioration est pense pour rpondre  vos besoins. Ce n'est pas une grande entreprise, mais un projet personnel men avec passion.",
