      "about_legal_desc": "Ce projet est une instance prive et n'est pas affili  Jellyfin ou The Movie Database (TMDB). Jelly utilise ces services pour enrichir son catalogue et amliorer l'exprience utilisateur, mais reste un projet indpendant dvelopp par Kaysuto Kimiya.",
