      "dmca_subtitle": "Notre engagement envers le respect de la proprit intellectuelle.",
      "dmca_important_title": "Avis Important",
      "dmca_important_desc": "Notre plateforme fonctionne comme un index et une base de donnes de contenu disponible publiquement sur Internet, agissant de manire similaire  Google. Nous ne stockons ni n'hbergeons aucun fichier vido ou mdia sur nos serveurs. Notre service se contente de cataloguer les mtadonnes et de fournir des liens vers des contenus hbergs par des tiers. Nous n'avons aucun contrle sur ces services tiers.",
      "dmca_notification_title": "Notification de Violation de Droits d'Auteur",
      "dmca_notification_desc": "Si vous tes titulaire de droits d'auteur et que vous pensez que du matriel disponible via notre service enfreint vos droits, vous pouvez soumettre une notification de violation de droits d'auteur. Votre notification doit inclure les lments suivants :",
      "dmca_requirement_1": "Une signature physique ou lectronique d'une personne autorise  agir au nom du titulaire d'un droit exclusif prtendument viol.",
      "dmca_requirement_2": "L'identification de l'uvre protge par le droit d'auteur qui aurait t viole.",
      "dmca_requirement_3": "L'identification du matriel qui est prtendument en infraction et qui doit tre retir, ainsi que des informations raisonnablement suffisantes pour nous permettre de localiser le matriel.",
