      "dmca_requirement_5": "Une dclaration selon laquelle vous estimez de bonne foi que l'utilisation du matriel de la manire incrimine n'est pas autorise par le titulaire du droit d'auteur, son agent ou la loi.",
      "dmca_requirement_6": "Une dclaration selon laquelle les informations contenues dans la notification sont exactes et, sous peine de parjure, que vous tes autoris  agir au nom du titulaire d'un droit exclusif prtendument viol.",
